div.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  color: #000000;
  font-size: 2vw;
}
div.loading b {
  transform: scaleY(0);
  animation: loading_in 1.5s infinite cubic-bezier(0.76, 0, 0.24, 1);
}
div.loading b:nth-child(1) {
  animation-delay: 0s;
}
div.loading b:nth-child(2) {
  animation-delay: 0.15s;
}
div.loading b:nth-child(3) {
  animation-delay: 0.3s;
}
div.loading b:nth-child(4) {
  animation-delay: 0.45s;
}
div.loading b:nth-child(5) {
  animation-delay: 0.6s;
}
div.loading b:nth-child(6) {
  animation-delay: 0.75s;
}
div.loading b:nth-child(7) {
  animation-delay: 0.9s;
}
div.loading b:nth-child(8) {
  animation-delay: 1.05s;
}
div.loading b:nth-child(9) {
  animation-delay: 1.2s;
}
div.loading b:nth-child(10) {
  animation-delay: 1.35s;
}
div.linesquare b {
  width: 2rem;
  height: 2rem;
  background-color: #ffffff;
}
@keyframes loading_in {
  0% {
      transform-origin: 50% 100%;
      transform: scaleY(0);
  }
  25% {
      transform-origin: 50% 100%;
      transform: scaleY(1);
  }
  75% {
      transform-origin: 50% 0%;
      transform: scaleY(1);
  }
  100% {
      transform-origin: 50% 0%;
      transform: scaleY(0);
  }
}
